/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps } from '@wordpress/block-editor';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */
import { Fragment } from '@wordpress/element';
import { InspectorControls, PanelColorSettings  } from '@wordpress/block-editor';
import { RangeControl, PanelBody, ToggleControl } from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';

export default function Edit( props ) {

	const { attributes, setAttributes } = props;
	const {
		keyWordSearch,
		tripFact,
		tripTypeFilter,
		tripLocationFilter,
		priceOrderFilter,
		priceRangeFilter,
		tripDateFilter,
		textColor,
		inputBackgroundColor,
		inputBorderColor,
		btnBorderColor,
		btnBackgroundColor,
		btnTextColor,
		btnBorderRadius
	} = attributes;

	const btnBackgroundColorSettings = [
		{
			value: btnBackgroundColor,
			onChange: colorValue => setAttributes({ btnBackgroundColor: colorValue }),
			label: __( 'Button Background Color', 'wp-travel-blocks' )
		}
	];

	const textColorSettings = [
		{
			value: textColor,
			onChange: colorValue => setAttributes({ textColor: colorValue }),
			label: __( 'Text Color', 'wp-travel-blocks' )
		}
	];

	const inputBackgroundColorSettings = [
		{
			value: inputBackgroundColor,
			onChange: colorValue => setAttributes({ inputBackgroundColor: colorValue }),
			label: __( 'Input Background Color', 'wp-travel-blocks' )
		}
	];

	const inputBorderColorSettings = [
		{
			value: inputBorderColor,
			onChange: colorValue => setAttributes({ inputBorderColor: colorValue }),
			label: __( 'Input Border Color', 'wp-travel-blocks' )
		}
	];

	const btnBorderColorSettings = [
		{
			value: btnBorderColor,
			onChange: colorValue => setAttributes({ btnBorderColor: colorValue }),
			label: __( 'Button Border Color', 'wp-travel-blocks' )
		}
	];

	const btnTextColorSettings = [
		{
			value: btnTextColor,
			onChange: colorValue => setAttributes({ btnTextColor: colorValue }),
			label: __( 'Button Text Color', 'wp-travel-blocks' )
		}
	];

	return (
		<div { ...useBlockProps() }>
			<Fragment>
				<InspectorControls>
					<PanelBody title={ __( 'Trip Filter Options', 'wp-travel-blocks' ) } >
						<ToggleControl
							label={ __( 'Keyword Search', 'wp-travel-blocks' ) }
							checked={ keyWordSearch }
							onChange={ () => setAttributes( { keyWordSearch: ! keyWordSearch } ) }
						/>
						<ToggleControl
							label={ __( 'Trip Fact', 'wp-travel-blocks' ) }
							checked={ tripFact }
							onChange={ () => setAttributes( { tripFact: ! tripFact } ) }
						/>
						<ToggleControl
							label={ __( 'Trip Type Filter', 'wp-travel-blocks' ) }
							checked={ tripTypeFilter }
							onChange={ () => setAttributes( { tripTypeFilter: ! tripTypeFilter } ) }
						/>
						<ToggleControl
							label={ __( 'Trip Location Filter', 'wp-travel-blocks' ) }
							checked={ tripLocationFilter }
							onChange={ () => setAttributes( { tripLocationFilter: ! tripLocationFilter } ) }
						/>
						<ToggleControl
							label={ __( 'Price Order Filter', 'wp-travel-blocks') }
							checked={ priceOrderFilter }
							onChange={ () => setAttributes( { priceOrderFilter: ! priceOrderFilter } ) }
						/>
						<ToggleControl
							label={ __( 'Price Range Filter', 'wp-travel-blocks' ) }
							checked={ priceRangeFilter }
							onChange={ () => setAttributes( { priceRangeFilter: ! priceRangeFilter } ) }
						/>
						<ToggleControl
							label={ __( 'Trip Date Filter', 'wp-travel-blocks' ) }
							checked={ tripDateFilter }
							onChange={ () => setAttributes( { tripDateFilter: ! tripDateFilter } ) }
						/>
						<RangeControl
							label={__('Button Border Radius', 'wp-travel-blocks')}
							onChange={ (value) => setAttributes( { btnBorderRadius: value } ) }
							shiftStep={ 1 }
							min={ 2 }
							max={ 100 }
							value={ btnBorderRadius }
						/>
						<PanelColorSettings
							title={__('Text Color', 'wp-travel-blocks')}
							colorSettings={textColorSettings}
							initialOpen={false}
						/>
						<PanelColorSettings
							title={__('Input Background Color', 'wp-travel-blocks')}
							colorSettings={inputBackgroundColorSettings}
							initialOpen={false}
						/>
						<PanelColorSettings
							title={__('Input Border Color', 'wp-travel-blocks')}
							colorSettings={inputBorderColorSettings}
							initialOpen={false}
						/>
						<PanelColorSettings
							title={__('Button Border Color', 'wp-travel-blocks')}
							colorSettings={btnBorderColorSettings}
							initialOpen={false}
						/>
						<PanelColorSettings
							title={__('Button Background Color', 'wp-travel-blocks')}
							colorSettings={btnBackgroundColorSettings}
							initialOpen={false}
						/>
						<PanelColorSettings
							title={__('Button Text Color', 'wp-travel-blocks')}
							colorSettings={btnTextColorSettings}
							initialOpen={false}
						/>
					</PanelBody>
				</InspectorControls>
			</Fragment>
			<ServerSideRender
				block="wptravel/trip-filters"
				attributes={ props.attributes }
			/>
		</div>
	);
}
